import React from 'react'
import {FormLayout, Radio, Select} from "@vkontakte/vkui"
import { Animate } from "react-show"

class TargetDonateAdditionalSetup extends React.Component {

    state = {
        isVisible: true
    }

    onChanged = (e) => {
        this.setState({isVisible: !this.state.isVisible})
      }
    
    render() {
        return(
            <FormLayout>
                <Select top="Автор">
                    <option value="gvencadze">Konstantin Gvencadze</option>
                    <option value="babazhanov">Руслан Бабажанов</option>
                </Select>

                <Radio name="radio" value="onAmount" onChange={this.onChanged}>Когда соберём сумму</Radio>
                <Radio name="radio" value="onDate" defaultChecked onChange={this.onChanged}>В определённую дату</Radio>
                
                <Animate   
                    show={this.state.isVisible} // Toggle true or false to show or hide the content!
                    duration={500}
                    style={{
                        height: "auto"
                    }}
                    start={{
                        // The starting styles for the hidden component.
                        opacity: 0,
                        height: 0
                      }}
                      enter={{
                        // These styles will be applied when the component enters
                        opacity: 1,
                        height: 'auto',
                      }}
                      leave={{
                        // these styles will be applied when the component leaves
                        opacity: 0,
                        height: 0,
                      }}
                >
                <Select
                    top="Дата окончания"
                    placeholder="Выберите дату"
                >
                    <option>14 сентября</option>
                    <option>15 сентября</option>
                    <option>16 сентября</option>
                    <option>17 сентября</option>
                    <option>18 сентября</option>
                    <option>19 сентября</option>
                    <option>20 сентября</option>
                </Select>
                </Animate>
            </FormLayout>
        )
    }
}

export default TargetDonateAdditionalSetup